import { KeyframeData, MediaKeyframe } from '../Interfaces/MediaInfo';
import Metadata from '../Interfaces/Metadata';

class MediaInfo {
    mimeType: string | null

    // 
    duration: number | undefined

    // 
    hasAudio: boolean | null

    // 
    hasVideo: boolean | null

    // 
    audioCodec: string | null

    // 
    videoCodec: string | null

    // 
    audioDataRate: number | null

    // /
    videoDataRate: number | null

    // /)
    audioSampleRate: number | null

    // 
    audioChannelCount: number | null

    // 
    width: number | null

    // 
    height: number | null

    // 
    fps: number | null

    // 
    profile: string | null

    // H264 profile, : baseline, extended, main, high  https://www.cnblogs.com/lidabo/p/7419393.html
    level: string | null

    // H264 level  https://www.cnblogs.com/lidabo/p/7419393.html
    refFrames: number | string | null

    // B  https://www.jianshu.com/p/a82da1d0d84f
    chromaFormat: number | null

    //   http://blog.chinaunix.net/uid-12947719-id-3413698.html : 4:2:0, 4:2:1
    sarNum: number | null

    //   https://www.nmm-hd.org/doc/FFmpegSource2  AviSynth
    sarDen: number | null

    //   https://www.nmm-hd.org/doc/FFmpegSource2  AviSynth
    metadata: Metadata | null

    // 
    segments: Array<any> | null

    // (flv.jssegment)
    segmentCount: number | null

    // 
    hasKeyframesIndex: boolean | null

    // 
    keyframesIndex: MediaKeyframe | null

    // 
    constructor() {
        this.mimeType = null;
        this.duration = undefined;

        this.hasAudio = null;
        this.hasVideo = null;
        this.audioCodec = null;
        this.videoCodec = null;
        this.audioDataRate = null;
        this.videoDataRate = null;

        this.audioSampleRate = null;
        this.audioChannelCount = null;

        this.width = null;
        this.height = null;
        this.fps = null;
        this.profile = null;
        this.level = null;
        this.refFrames = null;
        this.chromaFormat = null;
        this.sarNum = null;
        this.sarDen = null;

        this.metadata = null;
        this.segments = null; // MediaInfo[]
        this.segmentCount = null;
        this.hasKeyframesIndex = null;
        this.keyframesIndex = null;
    }

    /**
     * 
     */
    isComplete() {
        const audioInfoComplete = this.hasAudio === false
            || (this.hasAudio === true
                && this.audioCodec != null
                && this.audioSampleRate != null
                && this.audioChannelCount != null);

        const videoInfoComplete = this.hasVideo === false
            || (this.hasVideo === true
                && this.videoCodec != null
                && this.width != null
                && this.height != null
                && this.fps != null
                && this.profile != null
                && this.level != null
                && this.refFrames != null
                && this.chromaFormat != null
                && this.sarNum != null
                && this.sarDen != null);

        return (
            this.mimeType !== null
            && this.duration !== undefined
            && this.metadata !== null
            && this.hasKeyframesIndex !== null
            && audioInfoComplete
            && videoInfoComplete
        );
    }

    /**
     *  hasKeyframesIndex()  SEEK
     */
    isSeekable(): boolean {
        return this.hasKeyframesIndex === true;
    }

    /**
     * 
     * @param milliseconds 
     */
    getNearestKeyframe(milliseconds: number): KeyframeData | null {
        if(this.keyframesIndex == null) {
            return null;
        }

        const table = this.keyframesIndex;
        const keyframeIdx = this._search(table.times, milliseconds);

        return {
            index: keyframeIdx,
            milliseconds: table.times[keyframeIdx],
            fileposition: table.filepositions[keyframeIdx]
        };
    }

    /**
     * 
     * @param list 
     * @param value 
     */
    private _search(list: Array<number>, value: number): number {
        let idx = 0;

        const last = list.length - 1;
        let mid = 0;
        let lbound = 0;
        let ubound = last;

        if(value < list[0]) {
            idx = 0;
            lbound = ubound + 1; // skip search
        }

        while(lbound <= ubound) {
            mid = lbound + Math.floor((ubound - lbound) / 2);
            if(mid === last || (value >= list[mid] && value < list[mid + 1])) {
                idx = mid;
                break;
            } else if(list[mid] < value) {
                lbound = mid + 1;
            } else {
                ubound = mid - 1;
            }
        }

        return idx;
    }
}

export default MediaInfo;
